import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AddressComponent } from '../../shared/components/address/address.component';
import { HfButtonComponent, HfCardComponent, HfToastService } from '@monorepo/ui';
import { OrderDetailsBarComponent } from '../../shared/components/order-details-bar/order-details-bar.component';
import { URLS } from '../../shared/utils/urls';
import { AppService, StoreService } from '@monorepo/utils';
import { Subscription } from 'rxjs';
import { SharedService } from '../../shared/services/shared.service';

@Component({
  selector: 'app-confirm-address',
  standalone: true,
  imports: [AddressComponent, HfCardComponent, HfButtonComponent, OrderDetailsBarComponent],
  templateUrl: './confirm-address.component.html',
  styleUrl: './confirm-address.component.scss'
})

export class ConfirmAddressComponent implements OnInit, OnDestroy {

  orderParams: any;
  isAddressAvailable: boolean = false;
  selectedAddress: Subscription;

  constructor(
    private router: Router,
    private toastService: HfToastService,
    public storeService: StoreService,
    public sharedService: SharedService,
    public appService: AppService
  ) { }

  ngOnInit() {
    if (this.appService.isBrowserRendering) {
      this.storeService.getValueFromStore('orderParams', (orderParams) => {
        this.orderParams = orderParams;
      });
    }
    this.selectedAddress = this.sharedService.selectedAddress$.subscribe((address: any) => {
      if (Object.keys(address).length !== 0) {
        this.isAddressAvailable = true;
      } else {
        this.isAddressAvailable = false;
      }
    });
  }

  navigateToPaymentPage() {
    if (this.isAddressAvailable) {
      this.router.navigate([URLS.PAYMENT]);
    } else {
      this.toastService.toastMessage.next({ title: `Address Not Saved`, msg: `You’ve picked a ${this.orderParams['zipCode']} deal, but don’t have a saved address there.`, type: 'error' });
    }
  }

  ngOnDestroy() {
    if (this.selectedAddress) {
      this.selectedAddress.unsubscribe();
    }
  }
}
